.vuestic-tag {
  height: 32px;
  border-radius: 16px;
  padding: 5px 14px;
  color: $text-gray;
  border: solid 2px transparent;
  display: flex;
  align-items: center;
  margin-bottom: 5px;
  opacity: 1;
  &:not(:last-child) {
    margin-right: 15px;
  }
  &-deleted {
    opacity: 0;
    transition: all 0.5s ease;
  }
}

.vuestic-tag-primary {
  border-color: #4ae387;
  .vuestic-tag-button {
    color: #4ae387;
    &:hover {
      color: lighten(#4ae387, 15%);
    }
    &:focus, &:active {
      color: darken(#4ae387, 15%);
    }
  }
}

.vuestic-tag-danger {
  border-color: #e34a4a;
