import lazyLoading from "./lazyLoading";

export default {
  name: "kontrole",
  meta: {
    expanded: false,
    title: "Kontrole",
    class: "danger",
    show: true,
    iconClass: "fa fa-check-square-o"
  },

  children: [
    {
      name: "kontrole-pregled",
      path: "/kontrole/pregled",
      component: lazyLoading("kontrole/pregled/pregled"),
      meta: {
        title: "Pregled",
        requiredAuth: true
      }
    },
    {
      name: "kontrole-postavke",
      path: "/kontrole/postavke",
      component: lazyLoading("kontrole/postavke/postavke"),
      meta: {
        title: "Postavke",
        requiredAuth: true
      }
    }
  ]
};



